html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

:root {
  --size: 16px;
  --base-font-size: 24px;
  --base-radius: #95a5a6;
}

@media screen and (max-width: 1200px) {
  :root {
    --base-font-size: 21px;
  }
}

@media screen and (max-width: 600px) {
  :root {
    --base-font-size: 18px;
  }
}

* {
  padding: 0;
  margin: 0;
  position: relative;
}

body {
  touch-action: pan-y;
}

body,
input,
textarea {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

body {
  background: #fcfcfd;
  color: #000;
}

/* base text */
html,
body,
.jbx-button,
.jbx-input {
  font-size: var(--base-font-size);
  line-height: 1;
}

.jbx-code-snippet {
  border-radius: var(--size);
  font-family: monaco, monospace;
  border: none;
  width: 100%;
  font-size: calc(var(--size) - 3px);
  line-height: 1.33;
  padding: var(--size) calc(var(--size) * 1.2);
  background: #ecf0f1;
  color: #34495e;
  resize: none;
}
.jbx-code-snippet:focus {
  outline: none;
}

.jbx-code-area {
  border-radius: var(--size);
  font-family: monaco, monospace;
  border: none;
  width: 100%;
  font-size: calc(var(--size) - 3px);
  line-height: 1.33;
  padding: var(--size) calc(var(--size) * 1.2);
  background: #ecf0f1;
  color: #34495e;
  resize: none;
  min-height: 120px;
}
.jbx-code-area:focus {
  outline: none;
}

.jbx-button {
  appearance: none;
  user-select: none;
  cursor: pointer;
  display: inline-block;
  background-color: #eee;
  box-shadow: 3px 3px 0 #ccc;
  padding: 0 calc(var(--size) / 2);
  border: none;
  height: calc(var(--size) * 2.5);
}
.jbx-button:active {
  top: 1;
  left: 1;
  box-shadow: 2px 2px 0 #ccc;
}

.jbx-a {
  color: #000;
  box-shadow: var(--accent-color) 0 2px 0, inset var(--accent-color) 0 -2px 0;
  font-weight: 700;
  text-decoration: none;
}
.jbx-a:hover {
  color: '#34495e';
}

.jbx-h1,
.jbx-h2,
.jbx-h3,
.jbx-h4 {
  line-height: 1;
  padding: 0;
  margin: 0;
  font-weight: 600;
}

.jbx-h4 {
  font-size: calc(var(--base-font-size));
  letter-spacing: -0.11px;
}
.jbx-h3 {
  font-size: calc(var(--base-font-size) * 1.25);
  letter-spacing: -0.22px;
}
.jbx-h2 {
  font-size: calc(var(--base-font-size) * 1.5);
  letter-spacing: -0.33px;
}
.jbx-h1 {
  font-size: calc(var(--base-font-size) * 2.5);
  letter-spacing: -1px;
}

.jbx-small-text {
  font-size: calc(var(--base-font-size) * 0.8);
  line-height: calc(var(--base-font-size) * 0.6);
}

.jbx-container {
  max-width: 1080px;
  margin: 0 auto;
  padding: calc(var(--size) * 2.5) calc(var(--size) * 1.5) calc(var(--size) * 4);
}

.jbx-inline {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.jbx-stack {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.jbx-input {
  color: #777;
  -webkit-text-fill-color: #777;
  opacity: 1;
  flex: 1;
  display: block;
  appearance: none;
  border: none;
  border-radius: 0;
  height: calc(var(--size) * 2.5);
  background-color: #eee;
  padding: 0 calc(var(--size) * 0.5);
  box-shadow: inset rgba(0, 0, 0, 0.04) 3px 3px 0, inset rgba(0, 0, 0, 0.03) 0.5px 0.5px 0;
}

.jbx-dropzone {
  height: 120px;
  flex: 1;
  max-width: 320px;
  display: flex;
  text-align: center;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  border: 2px dashed #000;
  color: #000;
  padding: 0;
  cursor: pointer;
}
.jbx-dropzone:hover {
  border-color: #999;
}
.jbx-dropzone span {
  width: 100%;
  cursor: pointer;
}
.jbx-dropzone input {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  cursor: pointer;
}

