.jbx-range {
  flex: 1;
  width: 100%;
  appearance: none;
  background-color: var(--accent-color);
  height: var(--size);
  border-radius: 0;
}

.jbx-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  box-shadow: none;
  border-radius: 0;
  height: calc(var(--size) * 2);
  width: var(--size);
  background-color: #000;
  border: none;
}
.jbx-range::-moz-range-thumb {
  appearance: none;
  box-shadow: none;
  border-radius: 0;
  height: calc(var(--size) * 2);
  width: var(--size);
  background-color: #000;
  border: none;
}
.jbx-range::-webkit-slider-runnable-track {
  appearance: none;
}
.jbx-range:focus {
  outline: none;
}
